// @ts-nocheck
// 
import colorPicker from './components/colorPicker';

// 
const components = [
  colorPicker
];

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return;
  // 
  components.map(component => Vue.component(component.name, component));
};

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  //  install Vue.use() 
  install,
  // 
  colorPicker
};
